.content-area {
  height: 895px;
  width: 120px;
  position: relative;
  background-color: var(--color-navajowhite-100);
}
.content-area1,
.content-area2,
.content-area3,
.content-area4,
.content-area5 {
  height: 895px;
  width: 120px;
  position: absolute;
  margin: 0 !important;
  bottom: -1px;
  left: 120px;
  background-color: var(--color-darkkhaki-100);
}
.content-area2,
.content-area3,
.content-area4,
.content-area5 {
  left: 240px;
  background-color: var(--color-navajowhite-100);
}
.content-area3,
.content-area4,
.content-area5 {
  left: 360px;
  background-color: var(--color-darkkhaki-100);
}
.content-area4,
.content-area5 {
  left: 480px;
  background-color: var(--color-navajowhite-100);
}
.content-area5 {
  left: 600px;
  background-color: var(--color-darkkhaki-100);
}
.content-area10,
.content-area6,
.content-area7,
.content-area8,
.content-area9 {
  height: 895px;
  width: 120px;
  position: absolute;
  margin: 0 !important;
  right: 600px;
  bottom: -1px;
  background-color: var(--color-navajowhite-100);
}
.content-area10,
.content-area7,
.content-area8,
.content-area9 {
  right: 480px;
  background-color: var(--color-darkkhaki-100);
}
.content-area10,
.content-area8,
.content-area9 {
  right: 360px;
  background-color: var(--color-navajowhite-100);
}
.content-area10,
.content-area9 {
  right: 240px;
  background-color: var(--color-darkkhaki-100);
}
.content-area10 {
  right: 120px;
  background-color: var(--color-navajowhite-100);
}
.you-have-made {
  margin: 0;
}
.sweet-chocolate {
  margin: 0;
  font-size: var(--font-size-81xl);
}
.you-have-made-container {
  position: absolute;
  top: 24px;
  left: 183px;
  display: inline-block;
  width: 901px;
  height: 192px;
  z-index: 2;
}
.background-shape {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--color-saddlebrown-100);
  width: 100%;
  height: 100%;
  z-index: 1;
}
.chocolate-logo-alter-6 {
  position: absolute;
  top: 45px;
  left: 46px;
  width: 106px;
  height: 156px;
  object-fit: cover;
  z-index: 2;
}
.logo-area {
  position: absolute;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  width: 1055px;
}
.success-message {
  align-self: stretch;
  height: 256px;
  position: relative;
}
.button-shapes {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--color-saddlebrown-100);
  width: 437px;
  height: 132px;
  z-index: 1;
}
.go-again {
  position: absolute;
  top: 20px;
  left: 42px;
  display: inline-block;
  width: 375px;
  height: 140px;
  cursor: pointer;
  z-index: 2;
}
.buttons,
.or {
  position: relative;
}
.buttons {
  height: 160px;
  width: 437px;
  max-width: 100%;
}
.or {
  align-self: stretch;
  height: 98px;
  display: inline-block;
  flex-shrink: 0;
  z-index: 1;
}
.separator1 {
  width: 102px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-xl) 0 0;
  box-sizing: border-box;
  color: var(--color-saddlebrown-100);
}
.buttons-child,
.go-home {
  position: absolute;
  cursor: pointer;
}
.buttons-child {
  top: 0;
  left: 0;
  background-color: var(--color-saddlebrown-100);
  width: 411px;
  height: 132px;
  z-index: 1;
}
.go-home {
  top: 20px;
  left: 29px;
  display: inline-block;
  width: 375px;
  height: 140px;
  z-index: 2;
}
.buttons1 {
  height: 160px;
  width: 411px;
  position: relative;
  max-width: 100%;
}
.message-container,
.navigation2,
.success-modal {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.navigation2 {
  flex-direction: row;
  padding: 0 var(--padding-10xl) 0 0;
  box-sizing: border-box;
  gap: 52.5px;
  max-width: 100%;
}
.message-container,
.success-modal {
  flex-direction: column;
}
.message-container {
  align-self: stretch;
  gap: 55px;
  max-width: 100%;
}
.success-modal {
  padding: 204px 0 0;
  box-sizing: border-box;
  max-width: calc(100% - 356px);
  text-align: left;
  font-size: var(--font-size-61xl);
  color: var(--color-navajowhite-100);
  font-family: var(--font-unlock);
}
.content-area11 {
  height: 895px;
  width: 120px;
  position: relative;
  background-color: var(--color-darkkhaki-100);
  z-index: 1;
}
.sim-end {
  width: 1440px;
  height: 894px;
  max-width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 219px;
  box-sizing: border-box;
  position: relative;
  gap: 58px;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1025px) {
  .navigation2 {
    flex-wrap: wrap;
  }
  .sim-end {
    height: auto;
  }
}
@media screen and (max-width: 975px) {
  .content-area {
    display: none;
  }
  .go-again,
  .go-home,
  .or,
  .you-have-made-container {
    font-size: var(--font-size-21xl);
  }
  .success-modal {
    max-width: 100%;
  }
  .content-area11 {
    display: none;
  }
  .sim-end {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 725px) {
  .navigation2 {
    gap: var(--gap-7xl);
  }
  .message-container {
    gap: var(--gap-8xl);
  }
  .success-modal {
    padding-top: 133px;
    box-sizing: border-box;
  }
  .sim-end {
    gap: var(--gap-10xl);
  }
}
@media screen and (max-width: 450px) {
  .go-again,
  .go-home,
  .or,
  .you-have-made-container {
    font-size: var(--font-size-5xl);
  }
}
